import { useState, useRef } from 'react';
import DraggableItem from '../components/atoms/DraggableItem';
import DropZone from '../components/atoms/DropZone';
import GameHeader from '../components/molecules/GameHeader';
import { ChevronRight, Trophy } from 'lucide-react';
import { useToast } from "@/hooks/use-toast";
import { gameLevels, GameItem, GameSet } from '../data/gameLevels';

const Index = () => {
  const { toast } = useToast();
  
  const [currentLevel, setCurrentLevel] = useState(0);
  const [items, setItems] = useState(gameLevels[0].items);
  const [score, setScore] = useState(0);
  const [completedSets, setCompletedSets] = useState<string[]>([]);
  const [totalScore, setTotalScore] = useState(0);
  const dragItem = useRef<GameItem | null>(null);

  const currentGameLevel = gameLevels[currentLevel];
  const isLastLevel = currentLevel === gameLevels.length - 1;
  const isLevelComplete = completedSets.length === currentGameLevel.sets.length;
  const isGameComplete = isLastLevel && isLevelComplete;

  const handleDragStart = (item: GameItem) => {
    dragItem.current = item;
    console.log('Iniciando arrastre:', item.name);
  };

  const handleDrop = (setId: string) => {
    if (!dragItem.current) return;

    const item = dragItem.current;
    console.log('Soltando:', item.name, 'en conjunto:', setId);

    if (item.category === setId) {
      // Clasificaci√≥n correcta
      setItems(prev => prev.filter(i => i.id !== item.id));
      setScore(prev => prev + 10);
      
      toast({
        title: "¬°Excelente conexi√≥n neural! üß†",
        description: `${item.name} pertenece a ${currentGameLevel.sets.find(s => s.id === setId)?.name}`,
        duration: 2000,
      });

      // Verificar si el conjunto est√° completo
      const remainingItemsInSet = items.filter(i => i.category === setId && i.id !== item.id);
      if (remainingItemsInSet.length === 0 && !completedSets.includes(setId)) {
        setCompletedSets(prev => [...prev, setId]);
        toast({
          title: "¬°Conjunto completado! üéâ",
          description: "Has activado todas las conexiones sin√°pticas",
          duration: 3000,
        });
      }
    } else {
      // Clasificaci√≥n incorrecta
      toast({
        title: "Conexi√≥n sin√°ptica interrumpida",
        description: "Intenta con otro conjunto neural",
        duration: 2000,
        variant: "destructive"
      });
    }

    dragItem.current = null;
  };

  const handleNextLevel = () => {
    if (!isLastLevel) {
      setTotalScore(prev => prev + score);
      setCurrentLevel(prev => prev + 1);
      setItems(gameLevels[currentLevel + 1].items);
      setScore(0);
      setCompletedSets([]);
      
      toast({
        title: "¬°Nuevo nivel desbloqueado! üöÄ",
        description: `Nivel ${currentLevel + 2}: ${gameLevels[currentLevel + 1].title}`,
        duration: 3000,
      });
    }
  };

  const handleRestart = () => {
    setCurrentLevel(0);
    setItems(gameLevels[0].items);
    setScore(0);
    setTotalScore(0);
    setCompletedSets([]);
    
    toast({
      title: "¬°Juego reiniciado! üîÑ",
      description: "Comenzando desde el nivel 1",
      duration: 2000,
    });
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50 to-indigo-100 p-4">
      <div className="max-w-6xl mx-auto">
        <GameHeader 
          score={score} 
          completedSets={completedSets.length} 
          totalSets={currentGameLevel.sets.length}
          level={currentLevel + 1}
          totalScore={totalScore + score}
        />
        
        {/* Informaci√≥n del nivel actual */}
        <div className="bg-white rounded-2xl p-6 mb-6 shadow-lg border border-gray-200">
          <div className="flex items-center justify-between">
            <div>
              <h2 className="text-2xl font-bold text-gray-800 mb-2">
                Nivel {currentLevel + 1}: {currentGameLevel.title}
              </h2>
              <p className="text-gray-600">
                {currentGameLevel.description}
              </p>
            </div>
            <div className="text-4xl">
              üß†
            </div>
          </div>
        </div>
        
        {isGameComplete ? (
          <div className="text-center py-16">
            <div className="bg-white rounded-3xl p-8 shadow-2xl border border-green-200">
              <div className="text-6xl mb-4">üèÜüß†‚ú®</div>
              <h2 className="text-4xl font-bold text-green-600 mb-4">¬°Felicitaciones!</h2>
              <p className="text-xl text-gray-600 mb-6">
                Has completado todos los niveles del juego de neurociencia
              </p>
              <div className="text-3xl font-bold text-blue-600 mb-6">
                Puntuaci√≥n Total: {totalScore + score} puntos
              </div>
              <button
                onClick={handleRestart}
                className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-3 px-8 rounded-xl transition-colors flex items-center gap-2 mx-auto"
              >
                <Trophy className="w-5 h-5" />
                Jugar de Nuevo
              </button>
            </div>
          </div>
        ) : isLevelComplete ? (
          <div className="text-center py-16">
            <div className="bg-white rounded-3xl p-8 shadow-2xl border border-blue-200">
              <div className="text-6xl mb-4">üéâüß†</div>
              <h2 className="text-4xl font-bold text-blue-600 mb-4">¬°Nivel Completado!</h2>
              <p className="text-xl text-gray-600 mb-6">
                Has establecido todas las conexiones sin√°pticas correctamente
              </p>
              <div className="text-2xl font-bold text-green-600 mb-6">
                Puntuaci√≥n del Nivel: {score} puntos
              </div>
              {!isLastLevel ? (
                <button
                  onClick={handleNextLevel}
                  className="bg-green-500 hover:bg-green-600 text-white font-bold py-3 px-8 rounded-xl transition-colors flex items-center gap-2 mx-auto"
                >
                  Siguiente Nivel
                  <ChevronRight className="w-5 h-5" />
                </button>
              ) : (
                <p className="text-lg text-gray-600">
                  ¬°Has completado todos los niveles disponibles!
                </p>
              )}
            </div>
          </div>
        ) : (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {/* √Årea de elementos arrastrables */}
            <div className="space-y-6">
              <div className="bg-white rounded-2xl p-6 shadow-lg border border-gray-200">
                <h2 className="text-2xl font-bold text-gray-800 mb-4 flex items-center gap-2">
                  üß†
                  Elementos del Nivel
                </h2>
                <p className="text-gray-600 mb-6">
                  Arrastra cada elemento al conjunto correspondiente para activar las conexiones sin√°pticas
                </p>
                <div className="grid grid-cols-2 sm:grid-cols-3 gap-4">
                  {items.map((item) => (
                    <DraggableItem
                      key={item.id}
                      item={item}
                      onDragStart={handleDragStart}
                    />
                  ))}
                </div>
              </div>
            </div>

            {/* √Årea de conjuntos */}
            <div className="space-y-4">
              <h2 className="text-2xl font-bold text-gray-800 flex items-center gap-2">
                üéØ
                Conjuntos del Nivel
              </h2>
              {currentGameLevel.sets.map((set) => (
                <DropZone
                  key={set.id}
                  set={set}
                  onDrop={handleDrop}
                  isCompleted={completedSets.includes(set.id)}
                  itemCount={items.filter(item => item.category === set.id).length}
                />
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Index;

